.dropdown {
  left: 21%;
  position: relative;
  text-align: -webkit-center;
  top: 10vh;
  width: 50%;
  z-index: 1;
}

.origem {
  border: 2px solid black;
  cursor: pointer;
  left: 15px;
  position: relative;
}

.dropdown-menu.show {
  bottom: auto;
  left: 0;
  margin: 0;
  position: absolute;
  right: auto;
  text-align: center;
  top: 100;
  -webkit-transform: translate(209px, 39px);
          transform: translate(209px, 39px);
  z-index: 0;
}

.dropdown-menu.show:hover {
  border-color: black;
  box-sizing: border-box;
}

.itemDark {
