}

.meals {
  align-items: center;
  background: #0fa36b;
  color: #464646;
  display: flex;
  flex-direction: column;
  font-family: sans-serif;
  height: 100vh;
  justify-content: center;
  width: 100%;
}

.rocksGlass {
  -webkit-animation: shake 1s infinite ease;
          animation: shake 1s infinite ease;
  max-height: 80vh;
}

